/**
 * 
 * uni-appuView
 */

import {
	devUrl,
	prodUrl,
	contentType,
	showLoading,
	loadingText,
	loadingTime,
	headerTokenName,
	userTokenName,
	codeName,
	successCode,
	invalidCode,
} from '@/common/settings'

/**
 * 
 * @param {Object} options - 
 * @param {String} options.url - 
 * @param {String} options.method - 
 * @param {Object} options.data - 
 * @param {Object} options.header - 
 * @param {Boolean} options.loading - 
 * @param {String} options.loadingText - 
 * @param {Number} options.timeout - 
 * @returns {Promise} Promise
 */
const request = (options = {}) => {
	// 
	options.url = options.url || '';
	options.method = options.method || 'GET';
	options.data = options.data || {};
	options.header = options.header || {};
	options.loading = options.loading !== false;
	options.loadingText = options.loadingText || loadingText;
	options.timeout = options.timeout || 30000;

	// 
	if (options.loading) {
		uni.showLoading({
			title: options.loadingText,
			mask: true
		});
	}

	// URL
	const baseUrl = process.env.NODE_ENV === 'development' ? devUrl : prodUrl;
	const url = options.url.startsWith('http') ? options.url : (baseUrl + options.url);

	// Token
	const token = uni.getStorageSync('userInfo') ? 
		JSON.parse(uni.getStorageSync('userInfo'))[userTokenName] : '';

	// 
	const header = {
		'content-type': contentType,
		...options.header
	};
	
	// Token
	if (token) {
		header[headerTokenName] = token;
	}

	// 
	return new Promise((resolve, reject) => {
		uni.request({
			url,
			method: options.method,
			data: options.data,
			header,
			timeout: options.timeout,
			success: (res) => {
				// 
				let data;
				
				try {
					// JSON
					if (typeof res.data === 'string') {
						data = JSON.parse(res.data);
					} else {
						data = res.data;
					}
				} catch (e) {
					console.error('JSON:', e);
					uni.showToast({
						title: '',
						icon: 'none'
					});
					reject(e);
					return;
				}

				// 
				if (data[codeName] === successCode) {
					resolve(data);
				} else if (data[codeName] == 401 || data[codeName] == 403) {
					// 401403
					
					// 
					uni.removeStorageSync('userInfo');
					
					// 
					uni.showToast({
						title: '',
						icon: 'none'
					});
					
					// 
					setTimeout(() => {
						uni.reLaunch({
							url: '/subpkg_login/pages/login/login'
						});
					}, 1500);
					
					reject(data);
				} else if (data[codeName] === invalidCode) {
					// token
					uni.showToast({
						title: '',
						icon: 'none'
					});
					
					// 
					uni.removeStorageSync('userInfo');
					
					// 
					setTimeout(() => {
						uni.reLaunch({
							url: '/subpkg_login/pages/login/login'
						});
					}, 1500);
					
					reject(data);
				} else {
					// 
					uni.showToast({
						title: data.msg || '',
						icon: 'none'
					});
					reject(data);
				}
			},
			fail: (err) => {
				// 
				uni.showToast({
					title: '',
					icon: 'none'
				});
				reject(err);
			},
			complete: () => {
				// 
				if (options.loading) {
					uni.hideLoading();
				}
			}
		});
	});
};

/**
 * GET
 * @param {String} url - 
 * @param {Object} data - 
 * @param {Object} options - 
 * @returns {Promise} Promise
 */
const get = (url, data = {}, options = {}) => {
	return request({
		url,
		method: 'GET',
		data,
		...options
	});
};

/**
 * POST
 * @param {String} url - 
 * @param {Object} data - 
 * @param {Object} options - 
 * @returns {Promise} Promise
 */
const post = (url, data = {}, options = {}) => {
	return request({
		url,
		method: 'POST',
		data,
		...options
	});
};

/**
 * PUT
 * @param {String} url - 
 * @param {Object} data - 
 * @param {Object} options - 
 * @returns {Promise} Promise
 */
const put = (url, data = {}, options = {}) => {
	return request({
		url,
		method: 'PUT',
		data,
		...options
	});
};

/**
 * DELETE
 * @param {String} url - 
 * @param {Object} data - 
 * @param {Object} options - 
 * @returns {Promise} Promise
 */
const del = (url, data = {}, options = {}) => {
	return request({
		url,
		method: 'DELETE',
		data,
		...options
	});
};

/**
 * 
 * @param {String} url - 
 * @param {String} filePath - 
 * @param {String} name - key
 * @param {Object} formData - 
 * @param {Object} options - 
 * @returns {Promise} Promise
 */
const upload = (url, filePath, name = 'file', formData = {}, options = {}) => {
	const baseUrl = process.env.NODE_ENV === 'development' ? devUrl : prodUrl;
	const uploadUrl = url.startsWith('http') ? url : (baseUrl + url);
	
	// Token
	const token = uni.getStorageSync('userInfo') ? 
		JSON.parse(uni.getStorageSync('userInfo'))[userTokenName] : '';
		
	// 
	const header = {
		...options.header
	};
	
	// Token
	if (token) {
		header[headerTokenName] = token;
	}
	
	if (options.loading !== false) {
		uni.showLoading({
			title: options.loadingText || '...',
			mask: true
		});
	}
	
	return new Promise((resolve, reject) => {
		uni.uploadFile({
			url: uploadUrl,
			filePath,
			name,
			formData,
			header,
			success: (res) => {
				// JSON
				let data;
				try {
					data = JSON.parse(res.data);
				} catch (e) {
					data = res.data;
				}
				
				if (data[codeName] === successCode) {
					resolve(data);
				} else if (data[codeName] === invalidCode) {
					// token
					uni.showToast({
						title: '',
						icon: 'none'
					});
					
					// 
					uni.removeStorageSync('userInfo');
					
					// 
					setTimeout(() => {
						uni.reLaunch({
							url: '/subpkg_login/pages/login/login'
						});
					}, 1500);
					
					reject(data);
				} else {
					// 
					uni.showToast({
						title: data.msg || '',
						icon: 'none'
					});
					reject(data);
				}
			},
			fail: (err) => {
				uni.showToast({
					title: '',
					icon: 'none'
				});
				reject(err);
			},
			complete: () => {
				if (options.loading !== false) {
					uni.hideLoading();
				}
			}
		});
	});
};

export default {
	request,
	get,
	post,
	put,
	del,
	upload
} 