import {
	devUrl,
	prodUrl,
	contentType,
	showLoading,
	loadingText,
	loadingTime,
	headerTokenName,
	userTokenName,
	codeName,
	successCode,
	invalidCode,
} from '@/common/settings'


// vmvuethisvuextoken
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: process.env.NODE_ENV === 'development' ? devUrl : prodUrl, // 
		method: 'GET',
		// jsonJSON.parse()
		dataType: 'json',
		showLoading, // loading
		loadingText, // loading
		loadingTime, // ms
		originalData: false, // 
		loadingMask: true, // loading
		// 
		header: {
			'content-type': contentType
		},
	});

	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuexvm.vuex
		config.header[headerTokenName] = vm.userInfo?. [userTokenName] || '';

		// urlurlthis.$u.get(url)url
		// if(config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		// code401403
		if (res[codeName] == 401 || res[codeName] == 403) {
			// 
			uni.removeStorageSync('userInfo');
			
			// 
			uni.showToast({
				title: '',
				icon: 'none'
			});
			
			// 
			setTimeout(() => {
				uni.reLaunch({
					url: '/subpkg_login/pages/login/login'
				});
			}, 1500);
			
			return false;
		} else if (res[codeName] == successCode) {
			// rescoderesult
			// resthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res
		} else if (res[codeName] == invalidCode) {
			// token
			vm.$u.func.showToast({
				title: '',
				success: () => {
					// uView
					vm.$u.route('/subpkg_login/pages/login/login')
				}
			})
			return false
		} else {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			vm.$u.toast(res.msg)
			return false;
		}
	}
}

export default {
	install
}
